  //-----------------------------------------------------------------[ ANIMATION TRIGGERS TO RUN ON SCROLL ]
  var triggerFx = function () {

    window.requestAnimationFrame(function () {

      var scroll = $(window).scrollTop(),
        viewportHeight = $(window).height(),
        viewportWidth = $(window).width(),
        $hand = $('#mobile-hand');
    

      // if ( $('.debug-scroll').length < 1 ) { 
      // 	$('body').append('<div class="debug-scroll" style="position: fixed; bottom: 0; right: 0; background: black; color: white;">' + scroll + '</div>');
      // } else {
      // 	$('.debug-scroll').text(scroll);
      // }

      if (scroll > 40) {
        $('.header--global.js-start').removeClass('js-start').addClass('js-scrolled');
      } else {
        $('.header--global.js-scrolled').removeClass('js-scrolled').addClass('js-start');
      }

      if (support.transitions) {

        var handPos = (0 - (scroll/8)) - 15;
        $hand.css('bottom', handPos + 'px');

        if ( ! animating ) {

          newSection = ''; 
          
          $sections.each(function(e){

            var delay = (viewportWidth >= 1024) ? 2200 : 200;
            var $el = $(this),
            triggerZone = $el.offset().top - (viewportHeight / 2.5); //bottom 5%

            var forceTrigger = ((scroll - $el.offset().top) > (viewportHeight / 2)) ? true : false;
            if (scroll >= triggerZone || forceTrigger) {
              if ( ! $(this).hasClass('js-active')) {
                $(this).addClass('js-active js-transition');
                var _this = this;
                var finished = setTimeout(function(){
                  $(_this).removeClass('js-transition');
                }, delay);
              }
            } else {
              $(this).removeClass('js-active');
            }

          });

          // This section is used to control back and forth growth;
          // if (newSection !== currentSection) {
          //   $sections.removeClass('js-active');
          //   if (newSection) {
          //     $('#' + newSection).addClass('js-active');
          //   }
          //   currentSection = newSection;
          //   animating = setTimeout(function(){
          //     animating = false;
          //   }, 710);

          // }

        }

        $fx.each(function (e) {

          var zone = (viewportWidth >= 1024) ? 1.15 : 1.05;
          var $el = $(this),
            triggerZone = $el.offset().top - (viewportHeight / zone); //bottom 5%

          //if it's already on, skip it
          // if ($el.hasClass('fx--on')) return;

          //if you're a half screen past the item, force it to appear instantly
          var forceTrigger = ((scroll - $el.offset().top) > (viewportHeight / 2)) ? true : false;

          if ( $el[0].classList.contains('fx--auto') ) {
            forceTrigger = true;
          }

          //apply effects to this element
          if (scroll >= triggerZone || forceTrigger) {
            $el.addClass('fx--on');
          } else {
            $el.removeClass('fx--on');
          }

        });

      } else {
        //if transitions aren't supported, just add all the effects immediately to prevent pop-in when scrolling
        $fx.each(function (e) {
          var $el = $(this);
          $el.addClass('fx--on');
        });
      }
    });
  };

  var circleSizes = function() {

    $pointsCircles.each(function (e) {
      var $el = $(this),
          $list = $el.closest('div').find('.points--try'),
          size = (($list.outerHeight()/16) + 6);

      console.log(size);

      if ( size < 36 ) { size = 36; }
      size += 'rem';

      $el.css({ 'width' : size} );
      $el.css({ 'height' : size } );
    });

  };

  var $sections = $('.approach'),
      newSection = '',
      currentSection = '',
      animating = false;
  var $fx = $('.fx');
  var $pointsCircles = $('.circle-points');

  triggerFx();
  circleSizes();

  $(window).scroll(triggerFx);
  $(window).resize(circleSizes);

  //-----------------------------------------------------------------[ SMOOTH SCROLL TO ANCHORS ]
  $('a[href^="#"]').click(function (e) {
    e.preventDefault();
    var target = $(this).attr('href');
    if ($(target).length) {
      //scroll to target
      $('html, body').stop().animate({
        scrollTop: $(target).offset().top
      }, 600);
      $(this).blur();
    }
  });

  $('.js-toggle-nav').on('click', function (e) {
    e.preventDefault();
    $('.nav').toggleClass('js-active');
  });

  $('.js-show-sample').on('click', function (e) {

    e.preventDefault();

    var work = $(this).closest('.featured-title');
    work.find('.work-sample').addClass('js-active');
    $('body').addClass('js-freeze');

  });

  $('.work-sample').on('click', function (e) {

    if ($(e.target).find('.work-sample__page').length > 0) {
      closeSample();
    }

  });

  $('.js-close-sample').on('click', function () { closeSample(); });


